@import './grid';

* {
    box-sizing: border-box; }

body {
    font-family: monospace;
    background-color: #eee;
    margin: 40px; }

a {
    color: #000000; }

h2 {
    margin: 2em 0 1em;
    font-size: 1.8em; }

h3 {
    text-decoration: underline; }

.image_wrap {
    position: relative;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center; }

.image_wrap img {
    width: 100%;
    color: #000000;
    height: auto; }

.section {
    clear: both;
    max-width: 500px; }

.navbar-item {
    display: block;
    margin: 0.5em 0; }

.bottom5em {
    margin-bottom: 5em; }

.top4em {
    margin-top: 4em; }

.full_width {
    width: 100%; }

.placeholder {
    width: 100%;
    min-height: 150px;
    background: #ccc;
    display: flex;
    align-items: center;
    justify-content: center; }

.fat-p {
    font-size: 1.3em;
    margin: 0; }
